.main {
    grid-area: data;
    display: grid;
    grid-template-areas:
            "user message";
    grid-template-columns: 150px 3fr;
    grid-gap: 5px;
}

.messages {
    grid-area: message;
}

.users {
    grid-area: user;
    margin-left: 10px;

    }
.users a {
     text-decoration: none;
     color: black;
 }
.users a:hover {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.71);
}
.main textarea {
    margin-top: 20px;
    grid-area: textarea;
    width: 200px;
    height: auto;
    outline: none;
    -moz-appearance: none;
    border: 1px solid #999;
    resize: none;
}

.userList {
    text-decoration: none;
    text-decoration-color: black;
}